<template>
  <div
      :key="id"
      id="achievement"
      :class="{unlocked: secondsUntil <= 0, locked: secondsUntil > 0, visible: isVisible()}"
  >
    <div id="info-container">
      <div id="title" class="container">
        <div class="row">
          <div class="col-9">
            <h3>
              <font-awesome-icon v-if="category === C_MONEY" :key="C_MONEY" icon="dollar-sign" size="sm"></font-awesome-icon>
              <font-awesome-icon v-if="category === C_TIME" :key="C_TIME" icon="clock" size="sm"></font-awesome-icon>
              <font-awesome-icon v-if="category === C_CIGARETTES" :key="C_CIGARETTES" icon="smoking-ban" size="sm"></font-awesome-icon>
              <!--#pattern: template literal i18n combination-->
              {{$t(`achievementObjects[${id}]`)}}
            </h3>
          </div>
          <div class="col-3">
            <div :class="{animate: secondsUntil < 0 && isVisible()}" class="fave"></div>
            <!--<span v-if="secondsUntil < 0">-->
              <!--<font-awesome-icon :key="unlocked" icon="check" size="lg"></font-awesome-icon>-->
            <!--</span>-->
          </div>
        </div>
      </div>
      <div id="description">
        <i18n path="Achievement.description" tag="p">
          <span place="time" class="hl-number">{{Math.floor(trinity.time)}}. {{$t('day')}}</span>
          <span place="money" class="hl-number">{{$n(Math.floor(trinity.money), 'currency')}}</span>
          <span place="cigarettes" class="hl-number">{{$tc('cigarettes', Math.floor(trinity.cigarettes), {count: Math.floor(trinity.cigarettes)})}}</span>
        </i18n>
      </div>
    </div><!--end info-container-->
    <div id="image-container">
      <div id="image-frame">
        <div id="image">
          <glyph-category :category="category"></glyph-category>
        </div>
      </div>
      <div id="image-header" class="container">
        <div class="row">
          <div class="col-2 no-gutters">
            <span v-if="secondsUntil < 0">
              <font-awesome-icon key="unlocked" icon="check" size="lg"></font-awesome-icon>
            </span>
            <span v-else>
              <font-awesome-icon key="locked" icon="lock" size="lg"></font-awesome-icon>
            </span>
          </div>
          <!--// TODO (29.01.19) Plurals-->
          <div class="col-10">
            <strong>
              <span v-if="secondsUntil < 0">
                <i18n path="Achievement.timeDescriptionPast" tag="span">
                  <span place="days">{{daysUntil}}</span>
                  <span place="date">{{day}}</span>
                </i18n>
              </span>
              <span v-else>
                <i18n path="Achievement.timeDescriptionFuture" tag="span">
                  <span place="days">{{daysUntil}}</span>
                  <span place="date">{{day}}</span>
                </i18n>
              </span>
            </strong>
          </div>
        </div>
      </div><!-- end image-header-->

    </div><!--end image-container-->

    <!--<pre>{{allProps}}</pre>-->
  </div>
</template>

<script>
  import {C_MONEY, C_TIME, C_CIGARETTES} from '../achievements'
  import GlyphBadge from './GlyphBadge'
  import GlyphCategory from './GlyphCategory'
  import moment from 'moment'
  import {mapGetters} from 'vuex'

  export default {
    name: 'Achievement',
    props: {
      id: Number,
      secondsUntil: Number,
      condition: Number,
      category: String
    },
    components: {
      GlyphBadge,
      GlyphCategory
    },
    data () {
      return {
        C_TIME: C_TIME,
        C_CIGARETTES: C_CIGARETTES,
        C_MONEY: C_MONEY
      }
    },
    methods: {
      isVisible () {
        if (!this.$el) return false
        const el = this.$el
        const rect = el.getBoundingClientRect();
        const elemTop = rect.top;
        const elemBottom = rect.bottom;

        // Only completely visible elements return true:
        const isVisible = (elemTop >= 0) && (elemBottom <= window.innerHeight);
        // Partially visible elements return true:
        // isVisible = elemTop < window.innerHeight && elemBottom >= 0;
        return isVisible;
      }
    },
    computed: {
      ...mapGetters([
        'cigarettesPerSecond',
        'pricePerSecond',
        'cigarettesPerSecond',
        'cigarettePrice'
      ]),
      day () {
        return moment().add(this.secondsUntil, 'seconds').format('DD.MM.YYYY HH:MM')
      },
      daysUntil () {
        // get positive numbers
        const multiplicator = (this.secondsUntil > 0) ? 1 : - 1
        return Math.round(moment.duration(this.secondsUntil, 'seconds').asDays()) * multiplicator
      },
      trinity () {
        const trinity = (() => {
          switch (this.category) {
            case C_TIME:
              return {
                cigarettes: this.cigarettesPerSecond * this.condition,
                money: this.pricePerSecond * this.condition,
                time: moment.duration(this.condition, 'seconds').as('days')
              }
            case C_CIGARETTES:
              return {
                cigarettes: this.condition,
                money: this.cigarettePrice * this.condition,
                time: moment.duration(this.condition / this.cigarettesPerSecond, 'seconds').as('days')
              }
            case C_MONEY:
              return {
                cigarettes: this.condition / this.cigarettePrice,
                money: this.condition,
                time: moment.duration(this.condition / this.pricePerSecond, 'seconds').as('days'),
              }
            default:
              console.warn(`${this.category} not found`)
          }
        })()
        console.log(trinity)
        return trinity
      }
    }
  }
</script>

