/**
* Achievement List
*/
<template>
  <div id="achievements">
    <div
        v-for="achievement in achievementsTicking"
        :key="achievement.id"
        class="achievement"
    >
      <achievement
          :id="achievement.id"
          :secondsUntil="achievement.secondsUntil"
          :condition="achievement.condition"
          :category="achievement.category"
      >
      </achievement>

    </div>
  </div>
</template>

<script>
  import achievements, {C_MONEY, C_TIME, C_CIGARETTES} from '../achievements'
  import {mapGetters} from 'vuex'
  import GlyphBadge from './GlyphBadge'
  import Achievement from './Achievement'
  import moment from 'moment'

  export default {
    name: 'AchievementList',
    props: {
      'categories': Array,
      'order': Number // 1: ascendent; -1 descendent
    },
    components: {
      GlyphBadge,
      Achievement
    },
    data () {
      return {
        achievements: achievements,
        C_TIME: C_TIME,
        C_CIGARETTES: C_CIGARETTES,
        C_MONEY: C_MONEY
      }
    },
    methods: {
      getSeconds (achievement) {
        switch (achievement.category) {
          case C_TIME:
            return this.getSecondsUntilTimeAchievement(achievement.condition)
          case C_MONEY:
            return this.getSecondsUntilMoneyAchievement(achievement.condition)
          case C_CIGARETTES:
            return this.getSecondsUntilCigarettesAchievement(achievement.condition)
          default:
            console.warn(`${achievement.category} not found`)
        }
      },
      getSecondsUntilTimeAchievement (seconds) {
        return seconds - this.secondsSaved
      },
      getSecondsUntilMoneyAchievement (cents) {
        const remainingCents =  cents - this.moneySaved
        const remainingSeconds = remainingCents / this.pricePerSecond
        // console.log(`MONEY: ${this.moneySaved} / ${cents} (${remainingCents} || ${remainingSeconds})`)
        return Math.round(remainingSeconds)
      },
      getSecondsUntilCigarettesAchievement (cigarettes) {
        const remainingCigarettes = cigarettes - this.cigarettesSaved
        const remainingSeconds = remainingCigarettes / this.cigarettesPerSecond
        // console.log(`CIGARETTES: ${this.cigarettesSaved} / ${cigarettes} (${remainingCigarettes} || ${remainingSeconds})`)
        // console.table({cigarettesSaved: this.cigarettesSaved, cigarettes, remainingCigarettes, remainingSeconds})
        return Math.round(remainingSeconds)
      }
    },
    computed: {
      ...mapGetters([
        'secondsSaved',
        'moneySaved',
        'cigarettesSaved',
        'cigarettesPerSecond',
        'pricePerSecond'
      ]),
      // chain no. 1
      achievementsCategoriesApplied () {
        return this.achievements.filter(achievement => this.categories.includes(achievement.category))
      },
      // chain no. 2
      achievementsTicking () {
        return this.achievementsCategoriesApplied
          .map(achievement => {
            achievement.secondsUntil = this.getSeconds(achievement)
            return achievement
          })
          .sort((a, b) => {
            return (a.secondsUntil - b.secondsUntil) * this.order
          })
      }
    },
    filters: {
      formatTimedelta (seconds) {
        const timedelta = moment.duration(seconds)
        return `${timedelta.days()} tage ${timedelta.hours()} stunden ${timedelta.seconds()} sekunden`
      }
    }
  }
</script>

<style scoped>
  .achievement {
    padding: 1em;
    margin-bottom: 2em;
  }
</style>
