/* Vuex Ticks
 * A Vuex plugin which exposes a reactive time state variable
 * representing the seconds passed since the given `zeroPoint`
 * analog to the concept of a unix timestamp.
 */

/**
 * @function vuexTicks Vuex plugin definition
 * @param {integer} interval - Interval to autoupdate the current time variable
 * @param {string}  zeroPoint - rootState property key of the moment to count up from
 * @returns {store} store object defined by Vuex
 */

// IE8 and earlier shim
if (!Date.now) Date.now = () => new Date().getTime()

const secondsSince = (date) => {
  const milliseconds = Date.now() - Date.parse(date)
  return Math.round(milliseconds / 1000)
}

export default function vuexTicks (interval, zeroPoint) {
  return store => {
    // called when the store is initialized
    setInterval(() => store.dispatch('ticks/updateTicks'), interval)

    // defines ticks vuex module
    const ticks = {
      namespaced: true,
      state: {
        // seconds since zeroPoint
        ticks: 0
      },
      mutations: {
        setTicks (state, seconds) {
          state.ticks = seconds
        }
      },
      actions: {
        updateTicks ({state, commit, rootState}) {
          commit('setTicks', secondsSince(rootState[zeroPoint]))
        }
      }
    }

    // register the module
    store.registerModule('ticks', ticks)
  }
}
