<template>
  <div id="navbar">
    <data-form @close="DATA_FORM_DEACTIVATE"></data-form>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <router-link :to="{name: 'index', query: $route.query}" class="navbar-brand">Smoke-Free Dashboard</router-link>
      <!--hamburger/sandwich-->
      <button
          @click="collapsed = !collapsed"
          class="navbar-toggler"
          :class="{collapsed: collapsed}"
          type="button" data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          :aria-expanded="collapsed"
          aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div
          class="collapse navbar-collapse"
          :class="{show: collapsed}"
          id="navbarSupportedContent"
      >
        <ul class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link
                :to="{name: 'achievements', query: $route.query}"
                class="nav-link"
            >
              {{$t('Navbar.achievements')}}
            </router-link>
          </li>
          <li class="nav-item">
            <router-link
                :to="{name: 'result-text', query: $route.query}"
                class="nav-link"
            >
              {{$t('Navbar.resultText')}}
            </router-link>
          </li>
          <li class="nav-item">
            <router-link
                :to="{name: 'dashboard', query: $route.query}"
                class="nav-link"
            >
              {{$t('Navbar.counter')}}
            </router-link>
          </li>
        </ul>
        <button @click="DATA_FORM_ACTIVATE" type="button" role="button" class="btn btn-outline-primary">
          <font-awesome-icon icon="calculator" size="lg"></font-awesome-icon>
        </button>
      </div>
    </nav>
    <HUD></HUD>
  </div>
</template>

<script>
import DataForm from './DataForm'
import HUD from './HUD'
import {mutations} from '../store'
import {mapMutations} from 'vuex'

export default {
  name: 'Navbar',
  components: {
    DataForm,
    HUD
  },
  data () {
    return {
      collapsed: false
    }
  },
  methods: {
    // #pattern: constant mapMutations
    ...mapMutations({
      [mutations.DATA_FORM_ACTIVATE]: mutations.DATA_FORM_ACTIVATE,
      [mutations.DATA_FORM_DEACTIVATE]: mutations.DATA_FORM_DEACTIVATE
    })
  }
}
</script>

<style scoped>
  #navbar {
    border-top: 0.5rem solid #dce8e8;
  }
  .router-link-active {
    color: #6b77ff !important;

  }
  .nav-link:hover {
    color: black !important;
  }
</style>
