      </span>
    </div>
    <div id="day" class="hud-item">
      <span class="container icon-container">
        <font-awesome-icon icon="calendar-alt" size="sm"></font-awesome-icon>
      </span>
      <span class="container value-container">
        <strong>{{$tc('day')}} {{duration.asDays() | toInt}}</strong>
      </span>
    </div>
  </div>
</template>

<script>
  import {
    formatFilters
  } from '../mixins'
  import {
    mapGetters,
    mapState
  } from 'vuex'
  import {
    SECONDS
  } from '../constants'


  export default {
    name: 'HUD',
    mixins: [
      formatFilters
    ],
    methods: {
      dynamicTime (seconds) {
        // TODO (28.01.19) use i18n
        if (seconds <= SECONDS.minute) {
          return this.$tc('seconds', seconds, {count: seconds})
        }
        else if (seconds <= SECONDS.hour) {
          const unit = Math.round(seconds / SECONDS.minute)
          return this.$tc('minutes', unit, {count: unit})
        }
        else if (seconds <= SECONDS.day) {
          const unit = Math.round(seconds / SECONDS.hour)
          return this.$tc('hours', unit, {count: unit})
        }
        else if (seconds <= SECONDS.week) {
          const unit = Math.round(seconds / SECONDS.day)
          return this.$tc('days', unit, {count: unit})
        }
        else if (seconds <= SECONDS.month) {
          const unit = Math.round(seconds / SECONDS.week)
          return this.$tc('weeks', unit, {count: unit})
        }
        else if (seconds <= SECONDS.year) {
          const unit = Math.round(seconds / SECONDS.month)
          return this.$tc('months', unit, {count: unit})
        }
        else if (seconds >= SECONDS.year) {
          const unit = Math.round(seconds / SECONDS.year)
          return this.$tc('years', unit, {count: unit})
        }
      }
    },
    computed: {
      ...mapGetters([
        'duration',
        'secondsSaved',
        'cigarettesSaved',
        'moneySaved'
      ]),
      ...mapState([
        'lastCigarette'
      ])
    }
  }
</script>

