<template>
  <transition name="modal">
    <!--TODO: close-->
    <div
        @click="$emit('close')"
        class="modal-mask"
    >
      <div class="modal-wrapper">
        <div
            @click.stop
            class="modal-container"
        >

          <!--header-->
          <div class="modal-header">
            <slot name="header">
            </slot>
          </div>

          <!--body-->
          <div class="modal-body">
            <slot name="body">
            </slot>
          </div>

          <!--footer-->
          <div class="modal-footer">
            <slot name="footer">
              <button class="btn btn-outline-danger modal-default-button" @click="$emit('close')">
                OK
              </button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
  name: 'Modal',
  mounted: function () {
    // close on escape
    document.addEventListener('keyup', (event) => {
      if (event.keyCode === 27) this.$emit('close')
    })
  }
  }
</script>

