import Vue from 'vue'
import Vuex from 'vuex'
import VuexTicks from './vuex-ticks'
import moment from 'moment'

Vue.use(Vuex)

export const mutations = {
  DATA_FORM_TOGGLE: 'DATA_FORM_TOGGLE',
  DATA_FORM_ACTIVATE: 'DATA_FORM_ACTIVATE',
  DATA_FORM_DEACTIVATE: 'DATA_FORM_DEACTIVATE',
  LAST_CIGARETTE_SET: 'LAST_CIGARETTE_SET',
  PACK_PRICE_SET: 'PACK_PRICE_SET',
  PACK_AMOUNT_SET: 'PACK_AMOUNT_SET',
  CIGARETTES_PER_DAY_SET: 'CIGARETTES_PER_DAY_SET',
  SUBMIT_DATA_FORM: 'SUBMIT_DATA_FORM'
}

/* VuexNow(interval) - inverval: time in miliseconds for autoupdating the now variable */
const ticks = VuexTicks(1000, 'lastCigarette')

export default new Vuex.Store({
  plugins: [ticks],
  state: {
    dataFormActive: false,
    lastCigarette: null,
    packPrice: null,
    packAmount: null,
    cigarettesPerDay: null
  },
  mutations: {
    // #data-form
    [mutations.DATA_FORM_TOGGLE] (state) {
      state.dataFormActive = !state.dataFormActive
    },
    // #data-form
    [mutations.DATA_FORM_DEACTIVATE] (state) {
      state.dataFormActive = false
    },
    // #data-form
    [mutations.DATA_FORM_ACTIVATE] (state) {
      state.dataFormActive = true
    },
    // #data-form
    [mutations.LAST_CIGARETTE_SET] (state, lastCigarette) {
      state.lastCigarette = lastCigarette
    },
    // #data-form
    [mutations.PACK_PRICE_SET] (state, packPrice) {
      state.packPrice = packPrice
    },
    // #data-form
    [mutations.PACK_AMOUNT_SET] (state, packAmount) {
      state.packAmount = packAmount
    },
    // #data-form
    [mutations.CIGARETTES_PER_DAY_SET] (state, cigarettesPerDay) {
      state.cigarettesPerDay = cigarettesPerDay
    }
  },
  actions: {
    // argument destructuring
    [mutations.SUBMIT_DATA_FORM] ({commit}, {lastCigarette, packPrice, packAmount, cigarettesPerDay}) {
      console.log('%c%s', 'color: #3eaf7c;', mutations.SUBMIT_DATA_FORM)
      commit(mutations.LAST_CIGARETTE_SET, lastCigarette)
      commit(mutations.PACK_PRICE_SET, packPrice)
      commit(mutations.PACK_AMOUNT_SET, packAmount)
      commit(mutations.CIGARETTES_PER_DAY_SET, cigarettesPerDay)
    }
  },
  getters: {
    // #timereactive; #trinity: time
    duration: (state) => {
      return moment.duration(state.ticks.ticks, 'seconds')
    },
    // #timereactive; #trinity: time
    secondsSaved: (state, getters) => {
      return getters.duration.as('seconds')
    },
    // #timereactive; #trinity: money
    moneySaved: (state, getters) => {
      return state.ticks.ticks * getters.pricePerSecond
    },
    // #timereactive; #trinity: cigarettes
    cigarettesSaved: (state, getters) => {
      return state.ticks.ticks * getters.cigarettesPerSecond
    },
    cigarettesPerSecond: (state) => {
      return state.cigarettesPerDay / (24 * 60 * 60)
    },
    cigarettePrice: state => {
      return state.packPrice / state.packAmount
    },
    pricePerSecond: (state, getters) => {
      return getters.cigarettePrice * getters.cigarettesPerSecond
    }
  }
})
